<template>
  <div class="login">
    <div>
      <h2>Please Log In</h2>
      <div id="loginForm">
        <form @submit.prevent="fnLogin">
          <p>
            <input class="w3-input" name="uid" placeholder="Enter your ID" v-model="user_id"><br>
          </p>
          <p>
            <input name="password" class="w3-input" placeholder="Enter your password" v-model="user_pw" type="password">
          </p>
          <p>
            <button type="submit" class="w3-button w3-green w3-round">Login</button>
          </p>
          <p style="font-family: 나눔고딕">만약 계정이 없다면, <router-link to="/signup">회원가입</router-link>을 먼저 진행해주세요!</p>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      user_id: '',
      user_pw: ''
    }
  },
  methods: {
    fnLogin() {
      if (this.user_id === '') {
        alert('ID를 입력하세요.')
        return
      }

      if (this.user_pw === '') {
        alert('비밀번호를 입력하세요.')
        return
      }
      axios.get("/login", {
        params : {
          userId : this.user_id
        }
      })
          .then(response=>{
            console.log('response.data : ' + response.data);
            console.log(response.data.password);
            //this.user_pw = response.data;
            //alert(response.data);
            if (this.user_pw != response.data.password) {
              alert('아이디 또는 비밓번호가 잘못되었습니다.')
              return
            }
            alert('로그인 되었습니다.')
            this.$router.push('/?userId='+this.user_id + '&username=' + response.data.username);
          })
          .catch(err=>{
            alert('해당 아이디의 계정이 없습니다.')
            console.log(err.response);
          });

    },
  },
}
</script>

<style>
#loginForm {
  width: 500px;
  margin: auto;
}
</style>